@charset "utf-8";

// 추가 인클루드는 _setting 안에다가 하기 여기 인클루드는 _setting 하나만 나와있게 관리하기
@import 'dev/_src/_scss/_setting';


/* ==========================================================================
    Print styles.
    Inlined to avoid the additional HTTP request:
    http://www.phpied.com/delay-loading-your-print-css/
 ========================================================================== */

@media print {
    *,
    *:before,
    *:after {
        background: transparent !important;
        color: #000 !important; /* Black prints faster: http://www.sanbeiji.com/archives/953 */
        box-shadow: none !important;
        text-shadow: none !important;
    }

    a,
    a:visited {
        text-decoration: underline;
    }

    a[href]:after {
        content: " (" attr(href) ")";
    }

    abbr[title]:after {
        content: " (" attr(title) ")";
    }

    /*
     * Don't show links that are fragment identifiers,
     * or use the `javascript:` pseudo protocol
     */

    a[href^="#"]:after,
    a[href^="javascript:"]:after {
        content: "";
    }

    pre {
        white-space: pre-wrap !important;
    }
    pre,
    blockquote {
        border: 1px solid #999;
        page-break-inside: avoid;
    }

    /*
     * Printing Tables:
     * http://css-discuss.incutio.com/wiki/Printing_Tables
     */

    thead {
        display: table-header-group;
    }

    tr,
    img {
        page-break-inside: avoid;
    }

    p,
    h2,
    h3 {
        orphans: 3;
        widows: 3;
    }

    h2,
    h3 {
        page-break-after: avoid;
    }
}

// ******************** code comments ********************
// accordion
// ******************** code comments ********************
/*! 아코디언 스타일 */
.accrodion-container{}
.accrodion-list{}
.accrodion-item{}
.accrodion-btn{
    position: relative;
    display: block;
    background-color: bisque;
    @include rem(padding, 8px 20px 8px 20px);
    border-bottom:1px solid #000000;
    .hide-txt.close{display: inline-block;}
    .hide-txt.open{display:none;}
}
.accrodion-util{
    display: flex;
    align-items: center;
    justify-content: flex-end;
    .txt{
        display:inline-block;
        width:100%;
        flex:1 1 auto;
        @include rem(font-size, 16px);
        color:black;
        font-weight: 700;
        line-height: 1.35;
        background-color:aqua;
    }
    .bu{
        display:inline-block;
        flex:0 0 auto;
        @include rem(width, 8px);
        @include rem(height, 8px);
        @include rem(margin, -4px 2px 0 2px);
        @include rem(border-top, 2px solid #000000);
        @include rem(border-right, 2px solid #000000);
        transform: rotate(135deg);
    }
    .tag{
        display:inline-block;
        flex:0 0 auto;
        @include rem(min-width, 40px);
        @include rem(font-size, 11px);
        @include rem(padding, 4px 10px 4px 10px);
        @include rem(border-radius, 20px);
        text-align: center;
        color:#ffffff;
        font-weight: 500;
        background-color: green;
    }
    .ellipsis{
        overflow: hidden;
        text-overflow: ellipsis;
        white-space: nowrap;
    }
}
.accrodion-content{
    display:none;
    @include rem(padding, 38px 0 38px 0);
    background-color:cornsilk;
}

// 아코디언 열렸을 때
.accrodion-item.open{
    .accrodion-btn{
        .hide-txt.close{display: none;}
        .hide-txt.open{display:inline-block;}
    }
    .accrodion-content{
        display: block;
    }
}

