// ******************** code comments ********************
// icon
// 이미지 아이콘과 css 아이콘을 구분해서 관리하기
// 추가 스타일이 필요할 경우 프로젝트내에서 판단하여 클래스를 추가 하거나 타입을 추가 하기
// ******************** code comments ********************
/*! css 아이콘 스타일 - 화살표 */
.ico-arrow-style{
    display: inline-block;
    @include rem(width, 10px);
    @include rem(height, 10px);
    @include rem(border-top, 2px solid #000000);
    @include rem(border-right, 2px solid #000000);
    &.top {
        transform: rotate(-45deg);
    }
    &.down {
        transform: rotate(135deg);
    }
    &.left {
        transform: rotate(-135deg);
    }
    &.right {
        transform: rotate(45deg);
    }
    &.top-left {
        transform: rotate(-90deg) skew(-10deg, -10deg);
    }
    &.top-right {
        transform: rotate(0) skew(-10deg, -10deg);
    }
    &.bottom-left {
        transform: rotate(180deg) skew(-10deg, -10deg);
    }
    &.bottom-right {
        transform: rotate(90deg) skew(-10deg, -10deg);
    }
    &.top-narrow {
        transform: rotate(-45deg) skew(-15deg, -15deg);
    }
    &.top-wide {
        transform: rotate(-45deg) skew(7deg, 7deg);
    }
}

/*! css 아이콘 스타일 - X */
.ico-close-gray{
    display:inline-block;
    position: relative;
    @include rem(width, 16px);
    @include rem(height, 16px);
    &:before{
        content:'';
        width:100%;
        height:2px;
        position: absolute;
        top:50%;
        left:0;
        margin-top: -1px;
        background-color:gray;
        transition: all .3s ease-out;
    }
    &:after{
        content:'';
        width:2px;
        height:100%;
        position: absolute;
        top:0;
        left:50%;
        margin-left: -1px;
        background-color:gray;
        transition: all .3s ease-out;
    }
    transform: rotate(-45deg);
}

/*! images 아이콘 스타일 */
// 기본 이미지
.ico-download-black{
    display:inline-block;
    @include rem(width, 18px);
    @include rem(height, 17px);
    background:url('../images/common/ico_download_dark.png') no-repeat left center;
    @include rem(background-size, 18px 17px);
}
// 스프라이트 이미지
.ico-download-black02{
    display: inline-block;
    @include sprite-custom(rem, $pc-ico-download-dark);
}


// ******************** code comments ********************
// page navigation
// ******************** code comments ********************
/*! page navigation */
// .page-nav-container{}
.page-nav{
    width:100%;
    text-align:center;
    font-size:0;

    .page-item-container{
        display:inline-block;
        @include rem(margin, 0 34px);
    }

    // 페이징 숫자 영역
    .page-item{
        display:inline-block;
        @include rem(width, 40px);
        @include rem(height, 48px);

        .page-link{
            display:table;
            width:100%;
            height:100%;
            .txt{
                display:table-cell;
                @include rem(font-size, 18px);
                color: gray;
                transition: color .3s ease-out;
            }
            &:hover:not(.on),
            &:focus:not(.on),
            &:active:not(.on){
                .txt{
                    color: blue;
                }
            }
            &.on{
                .txt{
                    color: blue;
                    font-weight: 500;
                }
            }
        }
    }

    // 페이징 버튼 영역
    .page-util{
        display:inline-block;
        @include rem(width, 48px);
        @include rem(height, 48px);

        .page-link{
            display:table;
            width:100%;
            height:100%;
            border:1px solid #999999;
            transition: border .3s ease-out;
        }

        .ico-area{
            display:table-cell;
            width:100%;
        }
        .ico-page-arrow{
            display:inline-block;
            position: relative;
            @include rem(width, 8px);
            @include rem(height, 8px);
            @include rem(border-top, 2px solid #000000);
            @include rem(border-right, 2px solid #000000);
            transition: border .3s ease-out;
            &.left{
                transform: rotate(-135deg);
                @include rem(margin-right, -2px);

                // hover 애니메이션 관련 소스 필요 없을 경우 제거
                left:0;
                &:first-child{
                    transition: left .3s ease-out;
                }
                &:last-child{
                    transition: left .3s ease-out;
                }
            }
            &.right{
                transform: rotate(45deg);
                @include rem(margin-left, -2px);

                // hover 애니메이션 관련 소스 필요 없을 경우 제거
                right:0;
                &:first-child{
                    transition: right .3s ease-out;
                }
                &:last-child{
                    transition: right .3s ease-out;
                }
            }
        }

        &.prev{
            @include rem(margin-left, 8px);
        }
        &.next{
            @include rem(margin-right, 8px);
        }

        // 페이징 버튼 - 화살표 애니메이션 - 필요 없을 시 제거하기
        .page-link{
            &:hover:not(.disabled){
                .ico-page-arrow{
                    &.left{
                        left:-3px;
                    }
                    &.right{
                        right:-3px;
                    }
                }
            }
        }

        // 페이징 버튼 - 기본 마우스 hover 속성
        .page-link{
            &:hover:not(.disabled),
            &:focus:not(.disabled),
            &:active:not(.disabled){
                border-color: blue;
                .ico-page-arrow{
                    border-color: blue;
                }
            }
            // disabled 속성
            @include ui-disabled();
        }
    }
}

/*! page navigation - type02 */
.page-nav.type02{
    .page-item-container{
        .page-item{
            &:first-child{
                position: relative;
                @include rem(padding-right, 15px);
                &:after{
                    content:'';
                    @include rem(width, 1px);
                    @include rem(height, 20px);
                    position: absolute;
                    top:50%;
                    right:0;
                    @include rem(margin-top, -10px);
                    background-color:gray;
                    transform: rotate(0deg) skew(-20deg, 0deg);
                }
            }
            &:last-child{
                @include rem(padding-left, 15px);
            }
        }
    }
}

// ******************** code comments ********************
// scroll
// ******************** code comments ********************
/*! jquery scrollbar - customizing css - common */
.scrollbar-inner,
.scrollbar-outer{
    overflow:hidden;
}

/*! jquery scrollbar - customizing css - inner scroll */
.scrollbar-inner > .scroll-element .scroll-element_outer,
.scrollbar-inner > .scroll-element .scroll-element_track,
.scrollbar-inner > .scroll-element .scroll-bar {
    border-radius: 0;
    transition:background-color .3s ease-out;
}

.scrollbar-inner > .scroll-element .scroll-element_track,
.scrollbar-inner > .scroll-element .scroll-bar {
    -ms-filter:"progid:DXImageTransform.Microsoft.Alpha(Opacity=40)";
    filter: alpha(opacity=1);
    opacity: 1;
}

.scrollbar-inner > .scroll-element .scroll-element_track { background-color: rgba(0, 0, 0, 0.1); }
.scrollbar-inner > .scroll-element .scroll-bar { background-color: darkgray; }
.scrollbar-inner > .scroll-element:hover .scroll-bar { background-color: gray; }
.scrollbar-inner > .scroll-element.scroll-draggable .scroll-bar { background-color: lightgray; }

/*! jquery scrollbar - customizing css - outer scroll */
.scrollbar-outer > .scroll-element .scroll-element_outer,
.scrollbar-outer > .scroll-element .scroll-element_track,
.scrollbar-outer > .scroll-element .scroll-bar {
    border-radius: 0;
    transition:background-color .3s ease-out;
}

.scrollbar-outer > .scroll-element .scroll-element_track { background-color: rgba(0, 0, 0, 0.1); }
.scrollbar-outer > .scroll-element .scroll-bar { background-color: darkgray; }
.scrollbar-outer > .scroll-element .scroll-bar:hover { background-color: gray; }
.scrollbar-outer > .scroll-element.scroll-draggable .scroll-bar { background-color: lightgray; }


// ******************** code comments ********************
// swiper - 기본
// ******************** code comments ********************
// swiper 종류별로 .base-swiper 와 같이 클래스 만들고 상속 해서 사용하기
.default-swiper{
    .swiper-container{}
    .swiper-wrapper{}
    .swiper-slide{
        @include rem(width, 200px);
        @include rem(height, 200px);
        @include rem(margin-right, 20px);
        background-color:chocolate;
    }
    .swiper-pagination{
        // 블릿 버전
        &.swiper-pagination-bullets{}
        // 텍스트 숫자 버전
        &.swiper-pagination-fraction{}
        // 프로그래스바 버전
        &.swiper-pagination-progressbar{}
    }
    // 블릿 - 활성화
    .swiper-pagination-bullet-active{
        background-color:blue;
    }
    // 프로그래스바 - 활성화
    .swiper-pagination-progressbar-fill{
        background-color:blue;
    }
    // swiper 이전 버튼
    .swiper-button-prev{
        &:after{
            font-size:0;
            display: inline-block;
            @include rem(width, 20px);
            @include rem(height, 20px);
            @include rem(border-top, 2px solid #000000);
            @include rem(border-right, 2px solid #000000);
            transform: rotate(-135deg);
        }
    }
    // swiper 다음 버튼
    .swiper-button-next{
        &:after{
            font-size:0;
            display: inline-block;
            @include rem(width, 20px);
            @include rem(height, 20px);
            @include rem(border-top, 2px solid #000000);
            @include rem(border-right, 2px solid #000000);
            transform: rotate(45deg);
        }
    }
}

// ******************** code comments ********************
// tab
// ******************** code comments ********************
/*! 탭 메뉴 영역 - 탭 컨테이너 */
.tab-nav-container{
    // 스크롤 필요시 사용
    &.scroll{
        overflow-y: hidden;
        overflow-x: auto;
        white-space: nowrap;
        margin:0 auto;
        -webkit-overflow-scrolling: touch;
        // @include rem(width, 값설정);
        @include rem(width, 300px);

        // scroll 디자인 필요할 경우 (크롬전용)
        &::-webkit-scrollbar {
            height: 8px;
        }
        &::-webkit-scrollbar-thumb {
            background-color: rgb(134, 133, 133);
            border-radius: 10px;
            background-clip: padding-box;
            border: 1px solid transparent;
        }
        &::-webkit-scrollbar-track {
            background-color: red;
            border-radius: 10px;
            box-shadow: inset 0 0 3px white;
        }
    }
}

/*! 탭 메뉴 영역 - 기본 탭 - 균등 분할 */
.tab-nav{
    width:100%;
    display:table;
    table-layout: fixed;

    .tab-item{
        display:table-cell;
        @include rem(height, 70px);
        @include rem(padding, 0 1px);
        &:first-child{
            padding-left:0;
        }
        &:last-child{
            padding-right:0;
        }
    }
    .tab-link{
        display:table;
        width:100%;
        height:100%;
        text-align:center;
        background-color: gray;
        transition:background-color .2s ease-out;
        .hide-txt{display:none;}
        .txt{
            // 텍스트를 말줄임 ... 해야 할 경우 관련소스를 여기에 넣기
            display:table-cell;
            @include rem(padding, 10px);
            @include rem(font-size, 20px);
            color:lightgray;
        }

        &:hover,
        &:focus,
        &:active,
        &.active{
            background-color: blue;
            .txt{
                // font-weight:500;
                color:#ffffff;
            }
        }
        &.active{
            .hide-txt{display:inline-block;}
        }
    }

    // 탭 메뉴 불균등 분할 일때 추가 클래스
    &.unequal{
        display:inline-table;
        table-layout: auto;
    }
}

/*! 탭 메뉴 영역 - 탭 컨텐츠 영역 */
.tab-content{
    .tab-pane{
        display:none;
        &.active{
            display:block;
        }
    }
}


// ******************** code comments ********************
// table
// ******************** code comments ********************
/*! table - col 타입 1 */
.tbl-container01{
    width: 100%;
    &.scroll{white-space: nowrap;overflow: auto;}
}
// 셀 균등하게 만들때
.tbl-cell-equal{
    table-layout: fixed;
}
// 고정틀 + 스크롤 테이블
.tbl-fixed-scroll{
    border:1px solid gray;
    -webkit-overflow-scrolling: touch;
    overflow: auto;
    &.height01{
        @include rem(max-height, 300px);
    }
    table{
        border:0;
        white-space: nowrap;
        border-collapse: separate;
    }
    .sticky-thead01{
        position: sticky;
        top:0;
        z-index:5;
        @include rem(height, 25px);
    }
    .sticky-thead02{
        position: sticky;
        @include rem(top, 25px);
        z-index:4;
        @include rem(height, 25px);
    }
    .sticky-tbody01{
        position: sticky;
        top:0;
        left:0;
        z-index: 3;
        @include rem(min-width, 90px);
    }
    .sticky-tbody02{
        position: sticky;
        top:0;
        @include rem(left, 90px);
        z-index: 2;
        background-color: #ffffff;
    }
}

// col 타입
.tbl-col01{
    width:100%;
    text-align: center;
    th{
        background-color:#e5edf4;
        @include rem(padding,14px 16px);
        @include rem(font-size, 14px);
        font-weight: bold;
        color: #2a2c2e;
        text-align: center;
        &:last-child{
            border-right:0;
        }
        &.border-b{border-bottom: 1px solid #c5d5e3};
        &.border-r{border-right: 1px solid #c5d5e3};
    }
    td{
        @include rem(padding,14px 16px);
        @include rem(font-size, 14px);
        color: #606266;
        border-bottom:1px solid #e4e7ed;
        &.point{color: #c12526;}
    }
    tr{
        &.bg-yellow{background: #fff9ea;}
    }
    &.type02{
        text-align: left;
        th{background-color: initial;text-align: left;}
        tbody{
            tr{
                border-top: 1px solid #e4e7ed;
                &:last-child{
                    border-top: 1px solid #cacdd5;
                    border-bottom: 1px solid #cacdd5;
                }
            }
            td{border: none;}
        }
    }
}

// row 타입
.tbl-row01{
    width:100%;
    border:1px solid gray;
    th{
        background-color:cornsilk;
        @include rem(font-size, 24px);
        border-right:1px solid gray;
        border-bottom:1px solid gray;
    }
    td{
        @include rem(font-size, 18px);
        border-right:1px solid gray;
        border-bottom:1px solid gray;
        &:last-child{
            border-right:0;
        }
    }
    tr:last-child{
        th{
            border-bottom:0;
        }
        td{
            border-bottom:0;
        }
    }
}

// col + row 타입
.tbl-col-row01{
    width:100%;
    text-align: center;
    th{
        background-color:#e5edf4;
        @include rem(padding,14px 8px);
        @include rem(font-size, 14px);
        font-weight: bold;
        color: #2a2c2e;
        text-align: center;
        &.border-t{border-top: 1px solid #e4e7ed};
        &.border-r{border-right: 1px solid #e4e7ed};
    }
    td{
        @include rem(padding,14px 8px);
        @include rem(font-size, 14px);
        color: #606266;
        border-bottom:1px solid #e4e7ed;
        &.border-r{border-right: 1px solid #e4e7ed};
        &.point{color: #c12526;}
    }
    thead{
        th{color: #000;}
    }
